export default {
    path: "/home",
    name: "home",
    component: () => import("@/views/Home"),
    children: [
        {
            path: "wechat",
            name: "wechat",
            component: () => import("@/components/WeChat")
        },
        {
            path: "weibo",
            name: "weibo",
            component: () => import("@/components/WeiBo")
        },
        {
            path: "chrome",
            name: "chrome",
            component: () => import("@/components/Chrome")
        },
        // {
        //     path: "/home",
        //     redirect: "/home/wechat"
        // }
    ]
}