const state = {
    name: window.localStorage.getItem("username") || '',
    isAdmin: window.localStorage.getItem("isAdmin") || false,
    userList: JSON.parse(window.localStorage.getItem("userObj") || "[]"),
}

const mutations = {
    // 退出登录
    loginOut(state, payload) {
        state.userList.map(item => {
            if(item.isLogin === payload.isLogin) {
                payload.isLogin = false;
                return item;
            }
        })
        window.localStorage.setItem("userObj", JSON.stringify(state.userList))
    },

    // 登录
    login(state, payload) {
        state.userList.map(item => {
            if(payload.username === item.username && !payload.isLogin && !item.isLogin) {
                item.isLogin = true
                return item;
            }
        })
        window.localStorage.setItem("userObj", JSON.stringify(state.userList))
    },

    // 获取用户名
    getUsername(state, payload) {
        state.name = payload.name
        state.isAdmin = payload.isAdmin
    }
}

const getters = {

}

const actions = {}

export default {
    namespaced: true,
    state, mutations, actions, getters
}
